/*==== Toggle Sidebar Btn ====*/
.menu-btn {
  margin-right: 20px;
  transition: all 0.7s;
  font-size: 1.2em;
}

.menu-btn span {
  display: block;
  width: 20px;
  height: 2px;
  margin: 4px auto 0;
  transition: all 0.3s cubic-bezier(0.81, -0.33, 0.345, 1.375);
  background: #fff;
}

.menu-btn span:nth-of-type(2) {
  position: relative;
  width: 35px;
  transform: rotateY(180deg);
}

.menu-btn span:nth-of-type(2)::before, .menu-btn span:nth-of-type(2)::after {
  display: block;
  width: 6px;
  height: 2px;
  content: '';
  transition: all 0.7s;
  transform: rotate(45deg);
  background: #fff;
  position: absolute;
  top: 2px;
  left: 0;
}

.menu-btn span:nth-of-type(2)::after {
  transform: rotate(145deg);
  position: absolute;
  top: -2px;
  left: 0;
}

.menu-btn.active span:first-of-type {
  transform: translateY(12px);
}

.menu-btn.active span:nth-of-type(2) {
  transform: none;
}

.menu-btn.active span:last-of-type {
  transform: translateY(-12px);
}

